import { useEffect, useRef, useState, useMemo } from 'react';
import { gsap } from 'gsap';

const OptimizedBlurText = ({
  text = '',
  delay = 200,
  className = '',
  animateBy = 'words',
  direction = 'top',
  threshold = 0.1,
  rootMargin = '0px',
  stepDuration = 0.35,
  onAnimationComplete,
}) => {
  const elements = animateBy === 'words' ? text.split(' ') : text.split('');
  const [inView, setInView] = useState(false);
  const containerRef = useRef(null);
  const spanRefs = useRef([]);
  
  // Setup intersection observer to trigger animation when in view
  useEffect(() => {
    if (!containerRef.current) return;
    
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setInView(true);
          observer.unobserve(containerRef.current);
        }
      },
      { threshold, rootMargin }
    );
    
    observer.observe(containerRef.current);
    return () => observer.disconnect();
  }, [threshold, rootMargin]);
  
  // Animation with GSAP
  useEffect(() => {
    if (!inView || spanRefs.current.length === 0) return;
    
    // Initial state based on direction
    const fromY = direction === 'top' ? -50 : 50;
    
    // Create timeline for staggered animation
    const tl = gsap.timeline({
      onComplete: onAnimationComplete
    });
    
    // Set initial state
    gsap.set(spanRefs.current, {
      opacity: 0,
      y: fromY,
      filter: 'blur(10px)',
    });
    
    // Animate each span with staggered delay
    tl.to(spanRefs.current, {
      opacity: 0.5,
      y: direction === 'top' ? 5 : -5,
      filter: 'blur(5px)',
      duration: stepDuration,
      stagger: delay / 1000,
      ease: 'power2.out',
    }).to(spanRefs.current, {
      opacity: 1,
      y: 0,
      filter: 'blur(0px)',
      duration: stepDuration,
      stagger: delay / 1000,
      ease: 'power2.out',
    }, ">-0.1"); // Slight overlap for smoother animation
    
    return () => {
      tl.kill();
    };
  }, [inView, direction, delay, stepDuration, onAnimationComplete]);
  
  return (
    <p
      ref={containerRef}
      className={`blur-text ${className} flex flex-wrap`}
    >
      {elements.map((segment, index) => (
        <span
          className="inline-block will-change-transform"
          key={index}
          ref={el => spanRefs.current[index] = el}
          style={{
            opacity: 0,
            transform: `translateY(${direction === 'top' ? -50 : 50}px)`,
            filter: 'blur(10px)',
            transformOrigin: 'center',
            backfaceVisibility: 'hidden',
          }}
        >
          {segment === ' ' ? '\u00A0' : segment}
          {animateBy === 'words' && index < elements.length - 1 && '\u00A0'}
        </span>
      ))}
    </p>
  );
};

export default OptimizedBlurText;