'use client';

import { useRef, useEffect, useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { gsap } from 'gsap';
import { motion } from 'framer-motion';

const DesktopNavigation = ({ isScrolled }) => {
  const pathname = usePathname();
  const navRef = useRef(null);
  const [hoveredItem, setHoveredItem] = useState(null);

  const navItems = [
    { href: '/', label: 'Home' },
    { href: '/about', label: 'About' },
    { href: '/programs', label: 'Programs' },
    { href: '/facilities', label: 'Facilities' },
    { href: '/admissions', label: 'Admissions' },
    { href: '/affiliated-university', label: 'Affiliated University' },
    { href: '/contact', label: 'Contact' },
  ];

  // Light sweep animation
  useEffect(() => {
    const navItems = navRef.current?.querySelectorAll('.nav-item');
    if (!navItems) return;

    const tl = gsap.timeline({ repeat: -1, repeatDelay: 3 });
    
    navItems.forEach((item, index) => {
      tl.to(item, {
        backgroundImage: 'linear-gradient(90deg, transparent 0%, rgba(74, 222, 128, 0.3) 50%, transparent 100%)',
        backgroundSize: '200% 100%',
        backgroundPosition: '-100% 0',
        duration: 0.6,
        ease: 'power2.inOut',
      }, index * 0.1)
      .to(item, {
        backgroundPosition: '100% 0',
        duration: 0.6,
        ease: 'power2.inOut',
      }, index * 0.1 + 0.1)
      .set(item, {
        backgroundImage: 'none',
      }, index * 0.1 + 0.7);
    });

    return () => tl.kill();
  }, []);

  return (
    <nav ref={navRef} className="flex items-center space-x-6">
      {navItems.map((item, index) => {
        const isActive = pathname === item.href;
        
        return (
          <div key={item.href} className="relative">
            <Link
              href={item.href}
              className={`nav-item relative px-3 py-2 text-sm font-medium transition-all duration-300 rounded-lg overflow-hidden ${
                isActive ? 'text-green-400 font-semibold' : 'text-green-400/80 hover:text-white'
              }`}
              onMouseEnter={() => setHoveredItem(index)}
              onMouseLeave={() => setHoveredItem(null)}
            >
              {/* Light sweep background */}
              <span className="relative z-10">{item.label}</span>
              
              {/* Active indicator */}
              {isActive && (
                <motion.span 
                  className="absolute bottom-0 left-0 right-0 h-0.5 rounded-full bg-green-400"
                  layoutId="activeIndicator"
                  transition={{ type: "spring", stiffness: 300, damping: 30 }}
                />
              )}
            </Link>

            {/* Hover tooltip box */}
            {hoveredItem === index && (
              <motion.div
                initial={{ opacity: 0, y: 10, scale: 0.8 }}
                animate={{ opacity: 1, y: 0, scale: 1 }}
                exit={{ opacity: 0, y: 10, scale: 0.8 }}
                transition={{ duration: 0.2, ease: "easeOut" }}
                className="absolute top-full left-1/2 transform -translate-x-1/2 mt-2 z-50"
              >
                <div className="bg-green-600 text-white px-3 py-2 rounded-lg shadow-lg border border-green-500/30 backdrop-blur-sm">
                  <div className="text-sm font-medium whitespace-nowrap">
                    {item.label}
                  </div>
                  {/* Arrow */}
                  <div className="absolute -top-1 left-1/2 transform -translate-x-1/2 w-2 h-2 bg-green-600 rotate-45 border-l border-t border-green-500/30"></div>
                </div>
              </motion.div>
            )}
          </div>
        );
      })}
    </nav>
  );
};

export default DesktopNavigation;