'use client';

import { useEffect, useRef } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { gsap } from 'gsap';

const MobileSidebar = ({ isOpen, onClose }) => {
  const pathname = usePathname();
  const backdropRef = useRef(null);
  const sidebarRef = useRef(null);
  const headerRef = useRef(null);
  const navItemsRef = useRef([]);
  const footerRef = useRef(null);
  const applyBtnRef = useRef(null);

  const navItems = [
    { href: '/', label: 'Home' },
    { href: '/about', label: 'About' },
    { href: '/programs', label: 'Programs' },
    { href: '/facilities', label: 'Facilities' },
    { href: '/admissions', label: 'Admissions' },
    { href: '/affiliated-university', label: 'Affiliated University' },
    { href: '/contact', label: 'Contact' },
  ];

  // Close sidebar when route changes (only if actually navigating)
  const prevPathname = useRef(pathname);
  useEffect(() => {
    if (isOpen && pathname !== prevPathname.current) {
      onClose();
      prevPathname.current = pathname;
    }
  }, [pathname, isOpen, onClose]);

  // Prevent body scroll when sidebar is open
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }

    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  // GSAP animations for sidebar
  useEffect(() => {
    if (!backdropRef.current || !sidebarRef.current) return;

    // Create animations but don't play them yet
    const backdropAnimation = gsap.timeline({ paused: true })
      .fromTo(backdropRef.current,
        { opacity: 0 },
        { opacity: 1, duration: 0.3, ease: 'power2.out' }
      );

    const sidebarAnimation = gsap.timeline({ paused: true })
      .fromTo(sidebarRef.current,
        { x: '-100%' },
        { x: 0, duration: 0.3, ease: 'power2.out' }
      );

    const headerAnimation = gsap.timeline({ paused: true })
      .fromTo(headerRef.current,
        { opacity: 0, y: -20 },
        { opacity: 1, y: 0, duration: 0.3, delay: 0.1, ease: 'power2.out' }
      );

    const navItemsAnimation = gsap.timeline({ paused: true });
    navItemsRef.current.forEach((item, index) => {
      navItemsAnimation.fromTo(item,
        { opacity: 0, x: -20 },
        { opacity: 1, x: 0, duration: 0.3, delay: 0.1 + index * 0.05, ease: 'power2.out' },
        0
      );
    });

    const footerAnimation = gsap.timeline({ paused: true })
      .fromTo(footerRef.current,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.3, delay: 0.3, ease: 'power2.out' }
      );

    // Play or reverse animations based on isOpen state
    if (isOpen) {
      backdropAnimation.play();
      sidebarAnimation.play();
      headerAnimation.play();
      navItemsAnimation.play();
      footerAnimation.play();
    } else {
      backdropAnimation.reverse();
      sidebarAnimation.reverse();
      headerAnimation.reverse();
      navItemsAnimation.reverse();
      footerAnimation.reverse();
    }

    // Setup hover animation for apply button
    if (applyBtnRef.current) {
      const applyBtnHover = gsap.timeline({ paused: true })
        .to(applyBtnRef.current, {
          scale: 1.02,
          duration: 0.2,
          ease: 'power2.out'
        });

      applyBtnRef.current.addEventListener('mouseenter', () => applyBtnHover.play());
      applyBtnRef.current.addEventListener('mouseleave', () => applyBtnHover.reverse());
      applyBtnRef.current.addEventListener('mousedown', () => {
        gsap.to(applyBtnRef.current, { scale: 0.98, duration: 0.1 });
      });
      applyBtnRef.current.addEventListener('mouseup', () => {
        gsap.to(applyBtnRef.current, { scale: 1.02, duration: 0.1 });
      });
    }

    // Setup hover animation for close button
    const closeBtn = headerRef.current?.querySelector('.close-btn');
    if (closeBtn) {
      const closeBtnHover = gsap.timeline({ paused: true })
        .to(closeBtn, {
          scale: 1.1,
          rotation: 90,
          duration: 0.2,
          ease: 'power2.out'
        });

      closeBtn.addEventListener('mouseenter', () => closeBtnHover.play());
      closeBtn.addEventListener('mouseleave', () => closeBtnHover.reverse());
      closeBtn.addEventListener('mousedown', () => {
        gsap.to(closeBtn, { scale: 0.9, duration: 0.1 });
      });
      closeBtn.addEventListener('mouseup', () => {
        gsap.to(closeBtn, { scale: 1.1, duration: 0.1 });
      });
    }

    return () => {
      // Cleanup animations
      backdropAnimation.kill();
      sidebarAnimation.kill();
      headerAnimation.kill();
      navItemsAnimation.kill();
      footerAnimation.kill();
    };
  }, [isOpen]);

  if (!isOpen) return null;

  return (
    <>
      {/* Backdrop */}
      <div
        ref={backdropRef}
        className="fixed inset-0 bg-black/60 backdrop-blur-sm z-[60] lg:hidden opacity-0"
        onClick={onClose}
      />

      {/* Sidebar */}
      <div
        ref={sidebarRef}
        className="fixed top-0 left-0 h-full w-80 max-w-[85vw] z-[70] lg:hidden bg-black/90 backdrop-blur-md border-r border-green-500/20 mobile-sidebar will-change-transform"
        style={{
          boxShadow: '4px 0 30px rgba(0, 0, 0, 0.5)',
          transform: 'translateX(-100%)'
        }}
      >
        {/* Header */}
        <div
          ref={headerRef}
          className="flex items-center justify-between p-6 border-b border-green-900/30 opacity-0"
        >
          <div className="flex items-center space-x-3">
            <div className="w-10 h-10 bg-green-500/10 rounded-lg flex items-center justify-center border border-green-500/20">
              <span className="text-green-400 font-bold text-lg">FG</span>
            </div>
            <div>
              <h2 className="font-bold text-lg text-green-400">FG Science College</h2>
              <p className="text-sm text-green-400/80">Excellence in Education</p>
            </div>
          </div>

          <button
            className="w-8 h-8 flex items-center justify-center rounded-lg hover:bg-green-500/20 transition-colors close-btn"
            onClick={onClose}
            aria-label="Close menu"
          >
            <svg className="w-5 h-5 text-green-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Navigation */}
        <nav className="flex-1 px-6 py-6">
          <ul className="space-y-2">
            {navItems.map((item, index) => {
              const isActive = pathname === item.href;

              return (
                <li
                  key={item.href}
                  ref={el => navItemsRef.current[index] = el}
                  className="opacity-0"
                >
                  <Link
                    href={item.href}
                    className={`block px-4 py-3 rounded-xl text-base font-medium transition-all duration-200 relative overflow-hidden ${isActive
                      ? 'bg-green-500/20 text-green-300 border-l-4 border-green-400 font-medium'
                      : 'text-green-400/80 hover:bg-green-500/10 hover:text-green-300'
                      }`}
                    style={{ minHeight: '44px' }}
                  >
                    {/* Simplified hover effect */}
                    <div className="absolute inset-0 bg-gradient-to-r from-green-400/5 to-blue-400/5 opacity-0 hover:opacity-100 transition-opacity duration-200"></div>
                    <span className="relative z-10">{item.label}</span>
                  </Link>
                </li>
              );
            })}
          </ul>
        </nav>

        {/* Footer */}
        <div
          ref={footerRef}
          className="p-6 border-t border-green-900/30 opacity-0"
        >
          <div ref={applyBtnRef}>
            <Link
              href="/admissions"
              className="block w-full px-6 py-3 bg-green-600 text-white text-center font-semibold rounded-xl hover:bg-green-500 transition-colors relative overflow-hidden"
              style={{ minHeight: '44px' }}
            >
              <div className="absolute inset-0 bg-gradient-to-r from-green-400/10 to-blue-400/10 opacity-50"></div>
              <span className="relative z-10">Apply Now</span>
            </Link>
          </div>

          <div className="mt-4 text-center space-y-1">
            <p className="text-sm text-green-400/80">
              +91-123-456-7890
            </p>
            <p className="text-sm text-green-400/80">
              info@fgscience.edu.in
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default MobileSidebar;